import React from 'react'
import { Provider } from 'react-redux'
import thunkMiddleware from 'redux-thunk';
import { configureStore } from '@reduxjs/toolkit'

import { initTestQuestions } from './src/actions';
import getQuestionSets from './src/utils/question-handler';
import rootReducer from './src/reducers'
import './src/styles/index.sass'

function configureAppStore(preloadedState) {
  const store = configureStore({
    reducer: rootReducer,
    middleware: (getDefaultMiddleware) =>
      getDefaultMiddleware().concat(thunkMiddleware),
    preloadedState,
  })

  if (process.env.NODE_ENV !== 'production' && module.hot) {
    module.hot.accept('./src/reducers', () => store.replaceReducer(rootReducer))
  }

  return store
}

const store = configureAppStore()

store.dispatch(
  initTestQuestions(getQuestionSets())
);

const WithProvider = ({ element }) => {
  return (
    <Provider store={store}>
      {element}
    </Provider>
  )
}

export default WithProvider
